@import '~antd/dist/antd.css';

.App {
  padding: 36px;
  /* background: white; */

  &-logo {
    text-transform: uppercase;
  }

  
  &-image {
    // Position
    position: fixed;
    right: -480px;
    top: -500px;
    z-index: -1;

    // Size, blending, animation
    max-width: 1250px;
    mix-blend-mode: normal;
    animation: animation-spin infinite 180s linear;
  }

  &-content {
    overflow: scroll;
  }

  &-subheading {
    margin-top: 6px;
    font-size: 18px;
    text-transform: uppercase;
  }
  
  &-header {
    font-size: calc(10px + 2vmin);
  }
}

.transparent {
  position: absolute;
  animation: animation-spin-counter infinite 90s linear;
  z-index: -1;
  display: inline-block;
  mix-blend-mode: multiply;
  background-blend-mode: multiply;
  background-color: rgb(249, 212, 72);
}

.sleep, .svg-element {
  height: 1250px;
  width: auto;
}

.calendar-group {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: flex-start;
  /* height: 800px; */
  // width: 60vw;
}

.calendar {
  background: hsl(0, 0%, 99%);
  /* display: flex;
  flex-direction: row;  */
  /* justify-content: flex-start; */
  /* background: hsl(0, 0%, 99%); */
  height: 950px;
  width: 200px;
}



// keyframes mixin
@mixin keyframes($name) {
  @-webkit-keyframes #{$name} {
    @content;
  }
  @-moz-keyframes #{$name} {
    @content;
  }
  @-ms-keyframes #{$name} {
    @content;
  }
  @keyframes #{$name} {
    @content;
  }
}

@include keyframes(animation-spin) {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@include keyframes(animation-spin-counter) {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}

.charty {
  background: hsl(0, 0%, 99%);
  width: 600px;
  height: 600px;
}

